@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;800;900&family=Space+Grotesk:wght@500;600;700&display=swap');

:root {
  /* Platform Colors */
  --youtube-red: #FF0000;
  --instagram-pink: #DD2A7B;
  --instagram-orange: #F58529;
  --instagram-purple: #8134AF;
  --tiktok-cyan: #00F2EA;
  --tiktok-pink: #FF0050;
  --twitter-blue: #1DA1F2;

  /* Gradients */
  --gradient-primary: linear-gradient(135deg, #F58529, #DD2A7B, #8134AF);
  --gradient-dark: linear-gradient(135deg, #0f0f1a, #1a1a2e, #16213e);
  --gradient-card: linear-gradient(145deg, rgba(255,255,255,0.05), rgba(255,255,255,0.02));
  --gradient-glow: linear-gradient(135deg, rgba(245,133,41,0.2), rgba(221,42,123,0.2), rgba(129,52,175,0.2));

  /* Background */
  --bg-primary: #0a0a14;
  --bg-secondary: #12121f;
  --bg-card: #1a1a2e;
  --bg-card-hover: #222240;
  --bg-input: #16162a;

  /* Text */
  --text-primary: #ffffff;
  --text-secondary: #a0a0b8;
  --text-muted: #6b6b82;

  /* Accent */
  --accent-green: #00d68f;
  --accent-red: #ff3d71;
  --accent-yellow: #ffaa00;
  --accent-blue: #3366ff;

  /* Borders */
  --border-color: rgba(255,255,255,0.06);
  --border-glow: rgba(221,42,123,0.3);

  /* Spacing */
  --sidebar-width: 260px;
  --header-height: 64px;

  /* Shadows */
  --shadow-sm: 0 2px 8px rgba(0,0,0,0.3);
  --shadow-md: 0 4px 24px rgba(0,0,0,0.4);
  --shadow-lg: 0 8px 48px rgba(0,0,0,0.5);
  --shadow-glow: 0 0 30px rgba(221,42,123,0.15);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, sans-serif;
  background: var(--bg-primary);
  color: var(--text-primary);
  -webkit-font-smoothing: antialiased;
  overflow-x: hidden;
}

h1, h2, h3, h4, h5 {
  font-family: 'Space Grotesk', sans-serif;
  font-weight: 700;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
  font-family: inherit;
}

.app-layout {
  display: flex;
  min-height: 100vh;
}

.main-content {
  flex: 1;
  margin-left: var(--sidebar-width);
  padding: calc(var(--header-height) + 24px) 32px 32px;
  min-height: 100vh;
}

/* Cards */
.card {
  background: var(--bg-card);
  border: 1px solid var(--border-color);
  border-radius: 16px;
  padding: 24px;
  transition: all 0.3s ease;
}

.card:hover {
  background: var(--bg-card-hover);
  border-color: var(--border-glow);
  box-shadow: var(--shadow-glow);
  transform: translateY(-2px);
}

/* Platform Badge Colors */
.platform-youtube { color: var(--youtube-red); }
.platform-instagram { color: var(--instagram-pink); }
.platform-tiktok { color: var(--tiktok-cyan); }
.platform-twitter { color: var(--twitter-blue); }

.platform-bg-youtube { background: rgba(255,0,0,0.12); }
.platform-bg-instagram { background: rgba(221,42,123,0.12); }
.platform-bg-tiktok { background: rgba(0,242,234,0.12); }
.platform-bg-twitter { background: rgba(29,161,242,0.12); }

/* Buttons */
.btn {
  padding: 10px 20px;
  border-radius: 10px;
  font-weight: 600;
  font-size: 14px;
  transition: all 0.2s;
  display: inline-flex;
  align-items: center;
  gap: 8px;
}

.btn-primary {
  background: var(--gradient-primary);
  color: white;
}

.btn-primary:hover {
  opacity: 0.9;
  transform: translateY(-1px);
  box-shadow: 0 4px 20px rgba(221,42,123,0.3);
}

.btn-secondary {
  background: var(--bg-input);
  color: var(--text-primary);
  border: 1px solid var(--border-color);
}

.btn-secondary:hover {
  border-color: var(--border-glow);
}

/* Grid Layouts */
.grid-2 { display: grid; grid-template-columns: repeat(2, 1fr); gap: 20px; }
.grid-3 { display: grid; grid-template-columns: repeat(3, 1fr); gap: 20px; }
.grid-4 { display: grid; grid-template-columns: repeat(4, 1fr); gap: 20px; }

/* Stat Number */
.stat-number {
  font-family: 'Space Grotesk', sans-serif;
  font-size: 32px;
  font-weight: 800;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.stat-label {
  font-size: 13px;
  color: var(--text-secondary);
  margin-top: 4px;
  font-weight: 500;
}

/* Change indicators */
.change-positive {
  color: var(--accent-green);
  font-size: 13px;
  font-weight: 600;
}

.change-negative {
  color: var(--accent-red);
  font-size: 13px;
  font-weight: 600;
}

/* Scrollbar */
::-webkit-scrollbar { width: 6px; }
::-webkit-scrollbar-track { background: transparent; }
::-webkit-scrollbar-thumb {
  background: rgba(255,255,255,0.1);
  border-radius: 3px;
}
::-webkit-scrollbar-thumb:hover { background: rgba(255,255,255,0.2); }

/* Loading shimmer */
@keyframes shimmer {
  0% { background-position: -200% 0; }
  100% { background-position: 200% 0; }
}

.skeleton {
  background: linear-gradient(90deg, var(--bg-card) 25%, var(--bg-card-hover) 50%, var(--bg-card) 75%);
  background-size: 200% 100%;
  animation: shimmer 1.5s infinite;
  border-radius: 8px;
}

/* Responsive */
@media (max-width: 1200px) {
  .grid-4 { grid-template-columns: repeat(2, 1fr); }
  .grid-3 { grid-template-columns: repeat(2, 1fr); }
}

@media (max-width: 768px) {
  .main-content { margin-left: 0; padding: calc(var(--header-height) + 16px) 16px 16px; }
  .grid-4, .grid-3, .grid-2 { grid-template-columns: 1fr; }
  :root { --sidebar-width: 0px; }
}
